<template>
  <div>
    <content-title
      title="岗位管理"
      tips="岗位名称、邮件标题关键字、积分阈值、转发邮件设置">
      <div class="add-btn" @click="addPosition" v-if="user.roles.some(function(role) {
           return role.id === rolesCpt.admin
           })">+ 新增岗位
      </div>
    </content-title>
    <content-c>
      <el-table
        :data="posList"
        style="width: 100%">
        <el-table-column
          prop="name"
          label="岗位名称">
        </el-table-column>
        <el-table-column
          prop="department.name"
          label="所属部门">
        </el-table-column>
        <el-table-column
          prop="key"
          label="邮件标题关键字">
        </el-table-column>
        <el-table-column
          prop="integral"
          label="积分阈值(分)">
        </el-table-column>
        <el-table-column
          prop="mailSize"
          label="大小阈值(kb)">
        </el-table-column>
        <el-table-column
          label="操作"
          width="360"
          v-if="user.roles.some(function(role) {
           return role.id === rolesCpt.admin
           })">
          <template slot-scope="scope">
            <button class="com-btn com-detail" @click="handleDetail(scope.row, scope.column, scope.$index)">查看</button>
            <button class="com-btn com-edit" @click="handleEdit(scope.row)">编辑</button>
            <button v-if="scope.row.id !== adminPositionIdCpt" class="com-btn com-del" @click="handleDel(scope.row)">
              删除
            </button>
          </template>
        </el-table-column>
      </el-table>

      <div class="com-page-box">
        <div class="com-pages">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalElements"
            v-if="totalElements > this.pageSize">
          </el-pagination>
        </div>
      </div>
    </content-c>

    <!-- 查看弹窗 -->
    <com-dialog
      :show="detailDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}"
    >
      <el-form label-position="top" label-width="80px"
               :model="formAddPos"
               size="mini"
               ref="formAddPos2"
               :rules="editFormRules">
        <el-form-item label="岗位名称" prop="name">
          <el-input :disabled="true" v-model="formAddPos.name"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="department.id">
          <!--<el-input v-model="formAddPos.dep"></el-input>-->
          <el-select :disabled="true" v-model="formAddPos.department.id" placeholder="请选择部门" style="width: 100%">
            <el-option v-for="dep in departmentList" :label="dep.name" :key="dep.id" :value="dep.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮件标题关键字" prop="key">
          <el-input :disabled="true" v-model="formAddPos.key"></el-input>
        </el-form-item>

        <el-row>
          <el-col :span="10">
            <el-form-item label="积分阈值" prop="integral">
              <el-input :disabled="true" type="number" v-model="formAddPos.integral"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 50px 0 0 5px;font-size: 12px;color: #5a5e66">分</div>
          </el-col>
          <el-col :span="10">
            <el-form-item label="大小阈值" prop="mailSize">
              <el-input :disabled="true" type="number" v-model="formAddPos.mailSize"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 52px 0 0 5px;font-size: 12px;color: #5a5e66">kb</div>
          </el-col>
        </el-row>
      </el-form>

      <div slot="title" class="com-dialog-title t-1">
        查看岗位
        <!--<div class="close x-color-1" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px" type="primary" @click="closeDialog">确 定</el-button>
      </div>
    </com-dialog>

    <!-- 编辑弹窗 -->
    <com-dialog
      :show="editDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}"
    >
      <el-form label-position="top" label-width="80px"
               :model="formAddPos"
               size="mini"
               ref="formAddPos3"
               :rules="editFormRules">
        <el-form-item label="岗位名称" prop="name">
          <el-input v-model="formAddPos.name"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="department.id">
          <!--<el-input v-model="formAddPos.dep"></el-input>-->
          <el-select v-model="formAddPos.department.id" placeholder="请选择部门" style="width: 100%">
            <el-option v-for="dep in departmentList" :label="dep.name" :key="dep.id" :value="dep.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮件标题关键字" prop="key">
          <el-input v-model="formAddPos.key"></el-input>
        </el-form-item>

        <el-row>
          <el-col :span="10">
            <el-form-item label="积分阈值" prop="integral">
              <el-input type="number" v-model="formAddPos.integral"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 50px 0 0 5px;font-size: 12px;color: #5a5e66">分</div>
          </el-col>
          <el-col :span="10">
            <el-form-item label="大小阈值" prop="mailSize">
              <el-input type="number" v-model="formAddPos.mailSize"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 52px 0 0 5px;font-size: 12px;color: #5a5e66">kb</div>
          </el-col>
        </el-row>
      </el-form>

      <div slot="title" class="com-dialog-title t-2">
        编辑岗位
        <!--<div class="close x-color-2" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #FEC71E; border-color: #FEC71E;" type="primary"
                   @click="editPosSave('formAddPos3')">保 存
        </el-button>
      </div>
    </com-dialog>

    <!--新增岗位-->
    <com-dialog
      :show="addPosDialogVisible"
      title="标题"
      :toastShowStyle="{borderRadius: '10px', width: '40%'}">
      <el-form label-position="top" label-width="80px"
               :model="formAddPos"
               size="mini"
               ref="formAddPos"
               :rules="addFormRules">
        <el-form-item label="岗位名称" prop="name">
          <el-input v-model="formAddPos.name"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="department.id">
          <!--<el-input v-model="formAddPos.dep"></el-input>-->
          <el-select v-model="formAddPos.department.id" placeholder="请选择部门" style="width: 100%">
            <el-option v-for="dep in departmentList" :label="dep.name" :key="dep.id"
                       :value="dep.id.toString()"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮件标题关键字" prop="key">
          <el-input v-model="formAddPos.key"></el-input>
        </el-form-item>

        <el-row>
          <el-col :span="10">
            <el-form-item label="积分阈值" prop="integral">
              <el-input type="number" v-model="formAddPos.integral"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 50px 0 0 5px;font-size: 12px;color: #5a5e66">分</div>
          </el-col>
          <el-col :span="10">
            <el-form-item label="大小阈值" prop="mailSize">
              <el-input type="number" v-model="formAddPos.mailSize"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <div style="margin: 52px 0 0 5px;font-size: 12px;color: #5a5e66">kb</div>
          </el-col>
        </el-row>
      </el-form>

      <div slot="title" class="com-dialog-title t-3">
        新增岗位
        <!--<div class="close x-color-3" @click="closeDialog">x</div>-->
        <img class="close-img" src="../../../assets/icon/close.png" @click="closeDialog">
      </div>
      <div slot="footer" class="com-dialog-footer">
        <el-button style="width: 100px; background-color: #57CFCA; border-color: #57CFCA;" type="primary"
                   @click="saveAddPos('formAddPos')">确认添加
        </el-button>
      </div>
    </com-dialog>
  </div>
</template>
<script>
  import contentTitle from '../../componentsBody/contentTitle.vue'
  import contentC from '../../componentsBody/contentC.vue'
  //  import tableOperate from './main/tableOperate.vue'

  import comDialog from '../../../components/toast/dialog.vue'
  import { mapState, mapActions, mapMutations } from 'vuex'
  import { adminPositionId, roles } from '../../../utils/baseConfig'

  export default {
    name: '',
    props: {},
    data () {
      return {
        currentPage: 1,
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        detailDialogVisible: false,
        formLabelAlign: {
          department: {
            id: '',
            name: ''
          },
          name: '',
          key: '',
          integral: '',
          mailSize: '',
        },
        editDialogVisible: false,
        addPosDialogVisible: false,
        formAddPos: {
          department: {
            id: ''
          },
          name: '',
          key: '',
          integral: '',
          mailSize: '',
        },
        addFormRules: { // 新增部门校验规则
          name: [
            {required: true, message: '请输入岗位名称', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          'department.id': [
            {required: true, message: '请选择部门', trigger: 'change'}
          ],
          key: [
            {required: true, message: '请输入标题关键字', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          integral: [
            {required: true, message: '请输入积分阈值', trigger: 'blur'}
          ],
          mailSize: [
            {required: true, message: '请输入大小阈值', trigger: 'blur'}
          ]
        },
        editFormRules: { // 新增部门校验规则
          name: [
            {required: true, message: '请输入岗位名称', trigger: 'blur'},
          ],
//          'department.id': [
//            {required: true, message: '请选择部门', trigger: 'change'}
//          ],
          key: [
            {required: true, message: '请输入标题关键字', trigger: 'blur'}
          ],
          integral: [
            {required: true, message: '请输入积分阈值', trigger: 'blur'}
          ],
          mailSize: [
            {required: true, message: '请输入大小阈值', trigger: 'blur'}
          ]
        }
      }
    },
    computed: {
      ...mapState([
        'user'
      ]),
      ...mapState('position', [
        'posList',
        'totalElements',
        'totalPages',
        'addPosStatus',
        'addPosMsg',
        'delPosStatus',
        'delPosMsg',
      ]),
      ...mapState('department', [
        'departmentList'
      ]),
      adminPositionIdCpt () {
        return adminPositionId
      },
      rolesCpt () {
        return roles
      }
    },
    watch: {
      addPosStatus (d) {
        if (d) {
          this.ac_posList({
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.closeDialog()
          })
        }
      },
      addPosMsg (d) {
        this.$message(d)
      },
      delPosStatus (d) {
        if (d) {
          this.ac_posList({
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          })
        }
      }
    },
    methods: {
      ...mapActions('position', [
        'ac_posList',
        'ac_posAddAndModify',
        'ac_posDelete',
      ]),
      ...mapActions('department', [
        'ac_departmentList'
      ]),
      ...mapMutations('position', [
        'mut_posList',
      ]),
      handleDetail (row, column, index) {
//        console.log(row, column, index)
        this.formAddPos = {
          department: {
            id: row.department.id,
            name: row.department.name,
          },
          name: row.name,
          key: row.key,
          integral: row.integral.toString(),
          mailSize: row.mailSize.toString(),
        }
        this.detailDialogVisible = true
      },
      handleEdit (row) { // 编辑
        this.formAddPos = {
          id: row.id,
          department: {
            id: row.department.id,
            name: row.department.name,
          },
          name: row.name,
          key: row.key,
          integral: row.integral.toString(),
          mailSize: row.mailSize.toString(),
        }
        this.editDialogVisible = true
      },
      editPosSave (formName) {
        this.integralRule()
        this.mailRule()
        this.$nextTick(() => {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.ac_posAddAndModify({
                // this.formAddPos
                id: this.formAddPos.id,
                department: {id: this.formAddPos.department.id},
                mailSize: this.formAddPos.mailSize,
                integral: this.formAddPos.integral,
                key: this.formAddPos.key,
                name: this.formAddPos.name
              }).then(() => {
                this.closeDialog()
              })
            } else {
              console.log('error submit!!')
              return false
            }
          })
        })
      },
      handleDel (row) {
        this.$confirm('删除该岗位, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_posDelete({jobId: row.id})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_posList({
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_posList({
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      addPosition () { // 新增岗位按钮
        this.addPosDialogVisible = true
      },
      saveAddPos (formName) { // 新增岗位
        this.integralRule()
        this.mailRule()
        this.$nextTick(() => {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              this.ac_posAddAndModify(this.formAddPos)
            } else {
              console.log('error submit!!')
              return false
            }
          })
        })
      },
      closeDialog () {
        this.formAddPos = {
          department: {
            id: ''
          },
          name: '',
          key: '',
          integral: '',
          mailSize: '',
        }
        this.addPosDialogVisible = false
        this.editDialogVisible = false
        this.detailDialogVisible = false
      },
      integralRule () {
        if (!(/(^[0-9]\d*$)/.test(this.formAddPos.integral))) {
          this.formAddPos.integral = ''
        }
      },
      mailRule () {
        if (!(/(^[0-9]\d*$)/.test(this.formAddPos.mailSize))) {
          this.formAddPos.mailSize = ''
        }
      }
    },
    components: {
      contentTitle,
      contentC,
//      tableOperate,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_posList({
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
      this.ac_departmentList()
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {
      this.mut_posList([])
    },
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .add-btn {
    padding: 6px 10px;
    background-color: #57CFCA;
    color: #ffffff;
    width: 110px;
    border-radius: 20px;
    text-align: center;
    float: right;
    margin-right: 10px;
    margin-top: -45px;
    cursor: pointer;
  }

  .t-1 {
    background-color: #64A5E7;
  }

  .x-color-1 {
    color: #64A5E7;
  }

  .t-2 {
    background-color: #FEC71E;
  }

  .x-color-2 {
    color: #FEC71E;
  }

  .t-3 {
    background-color: #57CFCA;
  }

  .x-color-3 {
    color: #57CFCA;
  }

  .el-form--label-top .el-form-item__label {
    border: 2px solid red;
    padding: 0px !important;
  }
</style>



